@import url(https://fonts.googleapis.com/css2?family=Courier+Prime&display=swap);
* {
  margin: 0;
  padding: 0;
}

.App {
  padding: 20px;;
}

nav.navbar { 
  display: flex;
  padding: 20px;
  background: #686de0; 
}

nav.navbar .navbar-user{
  margin-left: 10px;
  color: white;
}

button.navbar-button {
  padding: 5px 20px;
  width: 100px;
  border-radius: 3px;
  margin: 0 10px;
}

#home-btn {
  color: white;
  text-decoration: none;
}
body {
    background-color: #626e7c;
    background: url(/static/media/main-game-bg.4c330917.png);
    background-repeat: no-repeat;
    background-position-x: center;
    background-color: #626d7a;
    margin-top:7vh;
}

@media (max-width:1380px){
    body{
        margin-top:45px;
        background: url(/static/media/bg-pattern.884e7d70.png);
        background-repeat: repeat;
    }

}

@media (max-width:900px){
    body{
        margin-top:30px;
    }
}

@media (max-width:576px){
    body{
        margin-top:10px;
    }
}

@media (max-width:420px){
    body{
        margin-top:0;
    }
}

hr {
	min-height: 8vw;
	border: 0;
}

body.home {
    background-color: #626e7c!important; 
    background: url();
}

p {
    font-family: 'Courier Prime';
    color: #acacac;
}


#form {
    padding:15%;
    text-align: center;
}

#signup-form {
    padding:15% 15%;
    text-align: center;
}

.form-control {
    text-align: center;
    background-color: #4f4f4f;
    border: 2px solid #262626;
    color: #acacac;
}

.form-control:focus {
    background-color: #4f4f4f;
    color: #acacac;
    border-color: #ffcc00;

}

.robot-controller-title {
    max-width:100%;
    vertical-align:bottom;
}

h1.countdown {
    font-weight: 500;
    font-size: 1.6rem;
    padding: 5% 0 5% 25%;
    font-family: 'Transformers Movie';
    letter-spacing: 0.1rem;
    color:#000; 
    height: 100%;
    position: absolute;
    top:17%;
    left:2%;
    visibility: hidden;
}

h1.countdown.red {
    font-weight: 500;
    font-size: 1.6rem;
    padding: 5% 0 5% 25%;
    font-family: 'Transformers Movie';
    color:#f00;  
}

h1 {
    margin-bottom: 0;
    font-weight: 500;
    font-family: 'Transformers Movie';
    /* color:#80D8FF;  */
}

.clock {
    width: 100%;
}


.container.container-body {
    max-width:1200px;
}

.container.deck-container {
    width: 500px;
    min-height:309px;
    position:relative;
    left:-40%;
    box-shadow: 4px 4px 6px rgba(0,0,0, .5);
}

.static-deck-container {
    max-width: 500px;
    margin-bottom:2%;
}

.static-deck-container > .first-row {
    padding-top:12%
}

@media(min-width:530px) {
    .static-deck-container > .first-row {
        padding-top:50px;
    }
}

.tile-bg {
    max-width: 100%;
    max-height: 100%;
}

.bg-85{
    max-width: 85%;
    max-height: 85%; 
}

.select-robot-body {
    background: url(data:image/png;base64,);
    background-size: 100%;
    background-repeat: repeat-y;
}

.deck-bg {
    min-height: 150px;
    padding: 0% 10% 0% 11%;
    background: url(/static/media/robot-controller.4cd7540a.png);
    background-size: 100%;
    background-repeat: no-repeat;
}

.robot-placeholder {
    min-height: 150px;
    padding: 0% 10% 0% 11%;
    background: url(/static/media/robots-placeholder.7b5f2b7a.png);
    background-size: 100%;
    background-repeat: no-repeat;
}

.robot-placeholder > .first-row {
    padding-top: 7%;
}

.chat-mid-res-bg {
    margin-left:510px;
    width:360px;
    height:306px;
    background: url(/static/media/chat_mid-res_bg.a22fd896.png);
    background-size: cover;
    background-repeat: no-repeat;
}

.no-gutters {
    margin-right: 0;
    margin-left: 0;
}
  
.col, [class*="col-"] {
      padding-right: 0;
      padding-left: 0;
}

.tile {
    background: url(/static/media/TileSep-54.1c574f02.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.new-game {
    margin-top: 20%;
    width:25%;
    cursor: pointer;
}

.rooms-container {
    border-radius:10px;
    max-width:90%;
    border: 2px solid #202020;
    background-color: #2f2f2f;
    margin-right:7%;
    margin-left:7%;
    margin-top:3%;
}

.rooms-container p:first-child {
    padding-top: 5%;
    text-align: center;
}

.rooms-container p {
    padding-bottom: 2%;
}

.rooms {
    background: url(/static/media/rooms-bg-2.1a790b97.png);
    background-repeat: no-repeat;
    background-size: contain;
    
}

.container.login-container {
    max-width:790px;
    padding: 100px 100px;
    background: url(/static/media/home-z0-bg.6d210c97.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.login {
    background: url(/static/media/home-login-bg.a3004d85.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.login-footer {
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: 100%; 
}

.login-inner-container{
    padding:0;
    max-width:770px;
}


.login-text {
    padding-top:10%;
    padding-left:8%;
    padding-right:8%;
    font-family: 'Courier Prime'
}

.green-water-right {
    background: url(/static/media/TileSep-58.57a84683.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.submitLoginBtn {
    background-image: url( /static/media/btn-login.b8f457fa.png); 
}

.submitSignupBtn {
    background-image: url( /static/media/btn-signup.6af93401.png); 
}

.submitPlayBtn {
    background-image: url( /static/media/btn-play.991fbe42.png); 
}

.submitLoginBtn, .submitPlayBtn, .submitSignupBtn {
        background-size: 100% 100%;
        background-repeat: no-repeat; 
        width:130px; /* 107 */
        height:60px; /* 47*/
        border:none;
        background-color: #2f2f2f;
        margin-bottom:15px;
}

@media (max-width:700px) {
    .submitLoginBtn, .submitPlayBtn, .submitSignupBtn {
        width:107px; /* 107 */
        height:47px; /* 47*/ 
    }
}

@media (max-width:476px) {
    .submitLoginBtn, .submitPlayBtn, .submitSignupBtn {
        width:79px; /* 107 */
        height:35px; /* 47*/ 
    }
}



.a-login {
    text-decoration:none;
    font-weight: 500;
    color: #ffcc00!important;
    cursor: pointer;
    transition: all 0.5s ease-in-out;

}

.a-login:hover {
    color: #f6a044!important;
    text-decoration: none;
}

.green-water-bottom-rigth {
    background: url(/static/media/TileSep-60.22f3ece8.png);
    background-repeat: no-repeat;
    background-size: cover;
}

.chat-container {
    background: url(/static/media/chat-bg.47715cc5.png);
    background-repeat: no-repeat;
    background-size:100%;
    height:100vh;
    width: 300px;
    margin-top:16px;
}

.robots-container {
    background: url(/static/media/robots-bg.daa3a4ab.png);
    background-repeat: no-repeat;
    background-size:100%;
    height:100vh;
    width: 200px;
    margin-top:16px;
}

.chat {
    margin-top: 85px;
    margin-left:15px;
    width: 251px;
    border: 2px solid #292929;
    border-radius: 10px;
    background-color: #373737;
    padding-top: 15px;
    padding-left: 15px;
}

.robots {
    margin-top: 85px;
    margin-left:28px;
}

.robots img {
    width: 166px;
    height: 68px;
    margin-bottom:5px;
}

@media (max-width: 1199px) {
    .container.container-body{
        max-width:900px;
    }
    .chat-container  {
      display:none;
    }
    .container.deck-container{
        left:-70%;
    }
}

@media (max-width:900px){
    .container.container-body {
        max-width:700px;
    }
    .robots-container{
        display:none;
    }
    .container.deck-container{
        left:-50%;
    }
    .chat-mid-res-bg {
        display:none;
    }

    .draggable-deck {
        display: none;
    }
}

@media(min-width:901px){
    .static-deck-container {
        display:none;
    }
}

@media(min-width: 1200px){
    .chat-mid-res-bg {
        display:none;
    }
}

@media(min-width:797px){
    img.login-btn {
        max-width:130px;
    }
}

/* Mobile */
@media (max-width: 796px) {
    p, .form-control {
        font-size: 2.1vw;
    }
    img.login-btn {
        max-width:130px;
    }
}


@media (max-width: 696px){
    .container.login-container {
        padding: 80px 80px;
    }
    img.login-btn {
        max-width:110px;
    }


}

@media (max-width: 576px){
    .container.login-container {
        padding: 50px 50px;
    }

    img.login-btn {
        max-width:90px;
    }


}

@media (max-width: 476px){
    .container.login-container {
        padding: 35px 35px;
    }

    img.login-btn {
        max-width:70px;
    }
}

@media (max-width: 376px){
    .container.login-container {
        padding: 35px 35px;
    }

    img.login-btn {
        max-width:50px;
    }
}
#draggable {
    position: absolute;
    z-index: 9;
    left:50%;
  }
  
#draggableheader {
    cursor: move;
    z-index: 10;
    height:20px;
    width:100%;
    margin-bottom: 7%;
}

@media(min-width:901px) and (max-width: 1199px) {

    #draggable {
      position: absolute;
      z-index: 9;
      left:41%;
      margin-right:16px;
    }
    
  #draggableheader {
      cursor: move;
      z-index: 10;
      height:20px;
      width:100%;
      margin-bottom: 7%;
  }
  .container.deck-container {
    width: 500px;
    position:relative;
    left:0;

  }
}

  @media (max-width: 590px) {
    #draggableheader {
        height:18px;
        margin-bottom: 6%;
    }
}

@media (max-width: 420px) {
    #draggableheader {
        height:12px;
      }
}
